.sidenav {

    width: 13em;
    transition: 0.3s all ease-in-out;
    height: 100vh;
    padding-top: 28px;
    padding: 12em 1em;
    border: 1px solid #fff;
    border-bottom-right-radius: 20px;

}


.sidenavClosed {
    transition: 0.5s ease-in-out 0.1s;
    width: 80px;
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 12em 1em;
    border: 1px solid #fff;
    border-bottom-right-radius: 20px;
    height: 100vh;
    box-shadow: 1px 1px 10px #fff;
}

.person {
    margin: 0px 20px 20px 0;
    display: flex;
    color: #fff;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
}

.person p {
    margin: 10px;
}

.person-1 {
    background-color: cornflowerblue;
}

.status {
    position: absolute;
    height: 14px;
    width: 14px;
    background-color: green;
    left: 0;
    top: 0;
    border-radius: 100%;
    border: 2px solid #fff;
}

.status-4 {
    background-color: orange;
}

.status-5 {
    background-color: grey;
}




.person-2 {
    background-color: crimson;
}

.person-3 {
    background-color: darkorchid;
}

.person-4 {
    background-color: pink;
}

.person-5 {
    background-color: orange;
}


.person p {
    margin-left: 6px;

}


.sidebar-pic {
    height: 60px;
    width: 50px;
    border-radius: 12px;
}



.sidenav-button {
    position: absolute;
    bottom: 5%;
    height: 50px;
    width: 50px;
    background-color: #f3f3f3;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

.sidenav-button button {
    border: none;
    background-color: #fff;
    cursor: pointer;
    border-radius: 10px;

}

.sidenav-button img {
    height: 30px;
    width: 50px;
    transition: 0.3s all ease-in-out 0.1s;

}

.rotate {
    transform: rotate(180deg);
}

.topic {
    position: absolute;
    top: 20px;
    left: 10px;
}


@media only screen and (max-width:1200px) {

    .sidenav-button {
        top: 4em;
    }

    .sidebar-pic {
        height: 50px;
        width: 40px;
        border-radius: 12px;
    }

    .sidenav {
        font-size: 0.8em;
        height: 50vh;
    }

    .sidenavClosed {
        font-size: 0.8em;
        height: 50vh;

    }

    

}

@media only screen and (min-height:600px) and (max-width:1024px){

    .sidenavClosed{
        height: 90vh;
    }

}
